* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

:root {
  font-size: 62.5%;
}

li {
  list-style: none;
  font-size: 1.6rem;
}

.wrapper {
  margin: 5rem 1.6rem;
  overflow-x: auto;
}

.wrapper-timeline {
  display: flex;
  align-items: flex-start;
  gap: 3rem;
  padding: 2rem 0;
  position: relative;
  width: max-content;
}

.wrapper-timeline::before {
  content: "";
  position: absolute;
  top: 2rem;
  left: 0;
  right: 0;
  height: 1rem;
  background-color: black;
  z-index: 0;
  width: 100%;
}

.wrapper-timeline li {
  position: relative;
  margin-top: 1.6rem;
  min-width: 16rem;
  max-width: 16rem;
  padding-left: 1rem;
  cursor: pointer;
  transition: transform 0.3s ease, opacity 0.3s ease;
}
.wrapper-timeline li.dragging {
  opacity: 0.5;
  transform: scale(1.05);
  border: 1px solid black;
}

.hovered {
  border-style: dashed;
}

.wrapper-timeline li::before {
  content: "";
  width: 1rem;
  height: 1rem;
  border: 5px solid black;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  transform: translateX(-50%);
  top: -2rem;
  left: 0.9rem;
}
